.box-foto {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: -1;
}

.img-perfil {
  width: 400px;
  border-radius: 50%;
  border: 10px solid var(--bg);
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-30%, -50%);
}